import { InjectionToken } from '@angular/core';

/**
 * Interface para dados de configurao do Version Tag
 * @interface
 */
export interface VersionTagConfig {

    /**
     * Propriedade que determina a verso da aplicao
     * @type {sring}
     */
    appVersion: string;
}

/**
 * Constante de configurao
 * @const
 */
export const VERSIONTAG_CONFIG = new InjectionToken<VersionTagConfig>('versiontag.config');
