import { Injectable } from '@angular/core';
import { CanActivate, CanActivateChild, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Authentication } from './authentication';
import { User } from './user';

/**
 * Guard que verifica se usurio est autenticado antes do usurio acessar uma rota. 
 * Se for verificar que o usurio no est autenticado,  feito um redirecionamento para o login.
 * @class
 */
@Injectable({
    providedIn: 'root',
})
export class AuthGuard implements CanActivate, CanActivateChild {

    /**
     * Mtodo construtor responsvel pela injeo do servio de autenticao
     * @constructor
     * @param {Authentication} auth
     */
    constructor(private authenticationService: Authentication<User>) {}

    /**
     * Mtodo responsvel por identificar se o usurio est autenticado
     * @public
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns boolean
     */
    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {
        if (this.authenticationService.isAuthenticated()) {
            return true;
        }
        this.authenticationService.redirect();
        return false;
    }

    /**
     * Mtodo responsvel por atuar somente em rotas filhas de modo independente
     * @public
     * @param {ActivatedRouteSnapshot} route
     * @param {RouterStateSnapshot} state
     * @returns boolean
     */
    canActivateChild(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {
        return this.canActivate(route, state);
    }

}
