import { User } from "./user";
import { Observable } from "rxjs";

/**
 * Classe abstrata para servio de autenticao
 * @class
 */
export abstract class Authentication<U extends User> {

    /**
     * Metodo responsvel por checar se o usurio esta autenticado na aplicao
     * @returns boolean
     * @abstract
     * @public
     */
    abstract isAuthenticated(): boolean;

    /**
     * Metodo responsvel pelo login da aplicao
     * @public
     */
    abstract login();

    /**
     * Metodo responsvel pelo logou da aplicao
     * @public
     * @returns void
     */
    abstract logout();

    /**
     * Metodo responsvel por obter os dados do usurio logado
     * @public
     * @return User
     */
    abstract getUser(): U;

    abstract redirect();

    abstract getLoginNotifications(): Observable<User>;

}
