import { InjectionToken } from '@angular/core';

/**
 * Interface para dados de configurao AuthConfig
 * @interface
 */
export interface AuthConfig {

    /**
     * Propriedade para URL base
     * @type {sring}
     */
    baseUrl: string;

    /**
     * Propriedade para URL de login
     * @type {sring}
     */
    loginUrl: string;

    /**
     * Rota de navegao quando o usurio for logado com sucesso na aplicao.
     * @type {sring}
     */
    loginSuccessRoute: string;

    /**
     * Propriedade para URL de logOut
     * @type {string}
     */
    logoutUrl: string;

    /**
     * Propriedade para URL de dados do usurio
     * @type {string}
     */
    detailsUrl: string;

    /**
     * Propriedade para definio de storage
     * @type {Storage}
     */
    storage: Storage;

    /**
     * Propriedade para identificar o idice do usurio no storage
     * @type {string}
     */
    userStorageIndex: string;

    /**
     * Propriedade para identificar o idice do token no storage
     * @type {string}
     */
    tokenStorageIndex?: string;

}

/**
 * Constante de configurao
 * @const
 */
export const AUTH_CONFIG = new InjectionToken<AuthConfig>('auth.config');
