import { HttpRequest, HttpHeaders } from "@angular/common/http";

/**
 * Classe abstrata para gerencia de tokens
 * @class
 */
export abstract class Token {

    /**
     * Metodo responsvel pela extrao de tokes do heaedrs das requisies
     * @public
     * @param {HttpHeaders} headers
     * @returns strng
     */
    abstract extractTokenFromHeaders(headers: HttpHeaders): String;

    /**
     * Metodo responsvel pela adio dos tokens no storage
     * @public
     * @param {String} token
     * @returns void
     */
    abstract storeToken(token: String);

    /**
     * Metodo responsvel por injetar token no header das requisies
     * @public
     * @param {HttpRequest<any>} request
     * @returns HttpRequest<any>
     */
    abstract setTokenInHeader(request: HttpRequest<any>): HttpRequest<any>;

    /**
     * Metodo responsvel por verificar a existencia de um token de autenticao
     * @returns boolean
     * @public
     */
    abstract hasToken(): boolean;

}
