import { Injectable, NgZone } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { NotificationProvider } from '../providers/notification.provider';
import { HttpGenericErrorType } from '../types/http-generic-error.type';
import { ErrorProvider } from '../providers/error.provider';

/**
 * Servio HttpGenericErrorService
 * @class
 */
@Injectable({
    providedIn: 'root',
})
export class HttpGenericErrorService implements ErrorProvider {

    /**
     * Metodo construtor responsvelpor injetar o servio NotificationProvider
     * @param {NotificationProvider} notification
     * @constructor
     */
    constructor(private notification: NotificationProvider, private ngZone: NgZone) { }

    /**
     * Metodo responsvel pela identificao de erros
     * @public
     * @param {Error | HttpErrorResponse} error
     * @returns Boolean
     */
    shouldHandle(error: Error | HttpErrorResponse): Boolean {
        return error instanceof HttpErrorResponse && 
            !(error.headers.get('Content-Type') === 'application/problem+json' && 
            error.error);
    }

    /**
     * Metodo pela adio de erros
     * @public
     * @param {HttpErrorResponse} error
     * @returns void
     */
    handle(error: HttpErrorResponse): void {
        const generic = this.getErrorByCode(error);
        this.ngZone.run(() => { 
            this.notification.addErrorMessage(generic.detail, generic.title);
        });
    }

    /**
     * Metodo para obter erros a partir de um cdigo
     * @public
     * @param {HttpErrorResponse} error
     * @returns HttpGenericErrorType
     */
    public getErrorByCode(error: HttpErrorResponse): HttpGenericErrorType {
        const generic = GENERIC_ERRORS[error.status];

        if (generic === undefined) {
            return new HttpGenericErrorType(
                error.status,
                error.statusText,
                error.message
            );
        }

        return generic;
    }

}
/**
 * Constante que define tipos de erros bsicos
 * @const GENERIC_ERRORS
 */
export const GENERIC_ERRORS = {
    401: new HttpGenericErrorType(401,
        'No Autenticado',
        'Essa requisio requer um usurio autenticado.'),

    403: new HttpGenericErrorType(403,
        'Proibido',
        'Essa requisio requer uma permisso no servidor da aplicao que o usurio no possui.'),

    404: new HttpGenericErrorType(404,
        'No encontrado',
        'Essa requisio no pode ser encontrada.'),

    500: new HttpGenericErrorType(500,
        'Erro desconhecido',
        'O servidor encontrou uma condio inesperada.'),

    502: new HttpGenericErrorType(502,
        'Bad Gateway',
        'O servidor recebeu uma resposta invlida de um servio que utilizou para cumprir a requisio'),

    503: new HttpGenericErrorType(503,
        'Servio Indisponvel',
        'O servidor no pode atender a requisio nesse momento. Esta  uma condio temporria, tente novamente mais tarde'),

    504: new HttpGenericErrorType(504,
        'Gateway Timeout',
        'O servidor no conseguiu receber uma resposta a tempo esperado por um servio que utilizou para cumprir a requisio')
};
